.plusbutton {
    background-color: aquamarine;
    border-radius: 20%;
    padding:10px;
    cursor:pointer;
}

.plusbutton:hover {
    background-color: aqua;
}

.brownbutton {
    background-color: grey;
    border-radius: 5px;
    padding:5px;
    cursor:pointer;
}

.brownbutton:hover {
    background-color: lightgrey;
}

.tooltipContainer {
    position:relative;
}

.tooltipContainer .tooltip {
    visibility: hidden;
    transition: opacity 1s;
    position:absolute;
    width: 120px;
    bottom: 100%;
    left: 50%;
    margin-left: -60px; 
}

.tooltipContainer:hover .tooltip {
    visibility: visible;
}

.tooltipContainer .tooltip:after {
    content: " ";
    position: absolute;
    top: 50%;
    left: 100%; /* To the right of the tooltip */
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent black transparent;
  
}